@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    font-family: 'Varela Round', sans-serif;
}
.flow-container{
    max-width: 700px;
    margin: 0 auto;
}

.content-box{
    border-radius: 10px;
    box-shadow: 0 3px 5px #dcdcdc;
    margin: 20px 10px 10px 10px;
    overflow: hidden;
}
.content-box .image-box{
    width: calc(100% + 28px);
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    margin: -14px;
    overflow: hidden;
}
.content-box .cover-img{
    width: 100%;
}

.content-box .profile-img{
    width: 140px;
    height: 140px;
    margin: -95px auto 0px 25px;
    border: 5px solid #fff;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
}


.para-box{
    padding: 0 10px 10px 10px;
}

.para-box p{
    font-weight: 500;
    color: #8a8a8a;
    font-size: 17px;
    line-height: 130%;
    margin-left: 20px;
}

.para-box .my-name{
    color: #484848;
    font-size: 22px;
    font-weight: 800;
    margin-bottom: 15px;
    font-weight: bold;
    margin-left: 20px;
}

.social{
    margin: -50px auto 0px 190px;
    border: 5px solid #fff;
    border-radius: 10px;
    position: relative;
    overflow: auto;
    white-space: nowrap;
}

.social a{
    display: inline-block;
    color: white;
    text-align: center;
    text-decoration: none;
}

.social img{
    transition: -webkit-filter 0.5s ease;
    transition: filter 0.5s ease;
    transition: filter 0.5s ease, -webkit-filter 0.5s ease;
    height: 27px;
    margin-right: 20px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    cursor: pointer;
}

.social img:hover{
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.para-box a{
    text-decoration: none;
    font-weight: bold;
    color: #3494ff;
    transition: color 0.5s ease;
}

.para-box a:hover{
    color: #B73636;
}

.copyr{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 11px;
    color: #8a8a8a;
    margin-bottom: 10px;
    text-align: center;
}

.sign-img{
    height: 50px;
    margin-left: 20px;
}

.my-proj{
    color: #484848;
    font-size: 22px;
    font-weight: 800;
    margin-bottom: 15px;
    font-weight: bold;
    margin-left: 20px;
}

.load-more{
    text-decoration: none;
    cursor: pointer;
    font-size: 13px;
    color: #8a8a8a;
    margin-bottom: 10px;
    text-align: center;
}

.fa-arrow-down{
    transition: all 0.5s ease-out;
}

.fa-arrow-down:hover{
    transform: rotate(360deg);
}

.no-decorations{
    text-decoration: none;
    font-size: 13px;
    color: #8a8a8a;
}
.content-box-project{
    transition: box-shadow 0.3s ease-in-out;
    border-radius: 10px;
    border: 2px solid #dcdcdc;
    box-shadow: 0 3px 5px #dcdcdc;
    margin: 20px 10px 10px 10px;
}

.active-project:hover{
    box-shadow: 0 3px 20px #dcdcdc;
}

.project{
    display: flex;
    flex-direction: row;
    padding: 10px 0 10px 10px;
}

.project img{
    height: 130px;
    object-fit: contain;
    max-width: 130px;
    border-radius: 10px;
}

.project .project-details{
    margin-left: 5px;
}

.project .project-details h3{
    color: #484848;
}

.project .project-details p{
    color: #8a8a8a;
    font-size: 14px;
    margin-top: -10px;
}

.project .project-details span{
    color: #999;
    font-size: 11px;
}
.content-box-support{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: -webkit-filter 0.5s ease-in-out;
    transition: filter 0.5s ease-in-out;
    transition: filter 0.5s ease-in-out, -webkit-filter 0.5s ease-in-out;
    border-radius: 10px;
    border: 2px solid #FFDD00;
    box-shadow: 0 3px 70px #FFDD00;
    margin: 20px 10px 10px 10px;
}

.content-box-support:hover{
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.support{
    display: flex;
    flex-direction: row;
    padding: 10px 0 0px 10px;
}

.support img{
    height: 85px;
    object-fit: contain;
    max-width: 85px;
    border-radius: 10px;
}

.support .support-details{
    margin-left: 15px;
}

.support .support-details h3{
    color: #484848;
    /* margin-top: -0.1px; */
}

.support .support-details p{
    color: #8a8a8a;
    font-size: 13px;
    margin-top: -10px;
}

.support .support-details span{
    color: #999;
    font-size: 11px;
}
.center-container{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.dev-img{
    height: 120px;
    object-fit: contain;
    border-radius: 15px;
}

